
/* Lightbox Overlay */
.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .lightbox-content {
        .lightbox-close {
            position: absolute;
            top: 30px;
            right: 30px;
            font-size: 28px;
            background: none;
            border: none;
            color: white;
            cursor: pointer;
        }
        .lightbox-counter {
            position: absolute;
            top: 30px;
            left: 30px;
            font-size: 14px;
            color: #fff;
        }
        .lightbox-prev, .lightbox-next {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            font-size: 28px;
            color: white;
            background: none;
            border: none;
            cursor: pointer;
            outline: none;
        }
        .lightbox-prev {
            left: 2%;
        }
        .lightbox-next {
            right: 2%;
        }

        .lightbox-image-container {
            img {
                max-width: 100%;
                max-height: 80vh;
            }
        }
        .lightbox-thumbnails {
            display: flex;
            justify-content: center;
            margin-top: 20px;

            img {
                width: 60px;
                height: 60px;
                object-fit: cover;
                margin: 0 5px;
                cursor: pointer;
                opacity: 0.6;
                transition: opacity 0.3s;

                &.active {
                    opacity: 1;
                    border: 2px solid #fff;
                }
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}

